// 列表表头
export const calculateHours = (time1, time2) => {
  const date1 = new Date(time1);
  const date2 = new Date(time2);

  const diff = Math.abs(date1.getTime() - date2.getTime());

  return (diff / (1000 * 60 * 60)).toFixed(2);
};

